interface ParentableFeature extends Feature {
  parent?: Feature;
}

const featuresByPath = new Map<string, ParentableFeature>();
agencyFeatures.forEach((topLevelFeature) => {
  if (topLevelFeature.subFeatures) {
    topLevelFeature.subFeatures.forEach((subFeature) => {
      featuresByPath.set(subFeature.path, { ...subFeature, parent: topLevelFeature });
      if (subFeature.subFeatures) {
        subFeature.subFeatures.forEach(subFeatureL2 =>
          featuresByPath.set(subFeatureL2.path, { ...subFeatureL2, parent: subFeature }));
      }
    });
  }
  featuresByPath.set(topLevelFeature.path, topLevelFeature);
});


export const getParentFeatureFromPath = (path: string): Feature | undefined => {
  const parentFeatures = agencyFeatures.filter(feature => feature.subFeatures && feature.subFeatures.length);
  const parentFeature = parentFeatures.find(feature => !!feature.path && path.includes(feature.path));
  return parentFeature;
};

const adjustPath = (pathName: string, matches: number) => {
  let path = pathName;
  for (let i = 0; i < matches; i += 1) {
    path = path.substr(0, path.lastIndexOf('/'));
  }
  // for (let i = 0; i < matches; i += 1) {
  //   path = `${path}/${idPathFragment}`;
  // }
  return path;
};

export const getFeaturePathFromPathName = (pathName: string): string | undefined => {
  return [...featuresByPath.keys()]
    .sort((a, b) => a.length > b.length ? -1 : 1)
    .find((key) => {
      const idMatches = key.match(/\/\{id\}/g);
      const adjustedPath = idMatches ? adjustPath(pathName, idMatches.length) : pathName;
      return adjustedPath.endsWith(key) || adjustedPath.includes(`${key}/`);
    });
};


