export const getFeatureFromPathName = (pathName: string): Feature | undefined => {
  const match = getFeaturePathFromPathName(pathName);
  return match ? featuresByPath.get(match) : undefined;
};

export const getFeatureTitleFromPathName = (pathName: string, includeParent: boolean = true): string | undefined => {
  const match = getFeatureFromPathName(pathName);

  return match ? getHierarchyTitle(match, includeParent) : undefined;
};

const getHierarchyTitle = (feature?: ParentableFeature, includeParent: boolean = true): string => {
  let title = '';
  if (feature) {
    title = feature.label || '';
    if (includeParent && feature.parent) {
      title = title ? ` | ${title}` : '';
      return `${getHierarchyTitle(featuresByPath.get(feature.parent.path), includeParent)}${title}`;
    }
  }
  return title;
};

export default agencyFeatures;