import styled from '@emotion/styled';
import { InputBase } from '@material-ui/core';
import { css } from 'emotion';
import React, { useMemo } from 'react';
import { MdSearch } from 'react-icons/md';
import { useLocation } from 'react-router';
import { useRecoilValue } from 'recoil';
import { headerTitleState } from 'state/HeaderTitleState';
import { bp } from 'theme';
import AccountProfileMenu from './AccountProfileMenu';
import { getFeatureTitleFromPathName } from './AgencyFeatures';

const TopBar = styled.div`
  padding: 8px;
  border-bottom: 1px solid rgba(0,0,0,0.12);
  display: flex;
  align-items: center;
`;

const SearchField = styled.div`
  position: relative;
  background-color: #eee;
  border-radius: 4px;
  width: 40px;
  ${bp.md} {
    width: 300px;
  }
  margin-right: 16px;
`;

const Fill = styled.div`
  flex-grow: 1;
  min-width: 0; // Required for overflow ellipsis to work
`;

const SearchIcon = styled.div`
  padding: 0 12px;
  height: 100%;
  position: absolute;
  pointer-events: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
`;

const SearchInputStyles = css`
  padding: 8px 8px 8px 40px;
`;

const Title = styled.h2`
  font-size: 18px;
  margin: 0 16px 0 10px;
  font-weight: normal;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
`;

function Header() {
  const { pathname } = useLocation();
  const headerTitleFromState = useRecoilValue(headerTitleState);

  const headerTitleFromPath = useMemo(
    () => {
      return getFeatureTitleFromPathName(pathname, false) || '';
    },
    [pathname],
  );

  return (
    <TopBar>
      <Fill><Title>{!headerTitleFromState ? headerTitleFromPath : headerTitleFromState}</Title></Fill>
      <SearchField>
        <SearchIcon>
          <MdSearch />
        </SearchIcon>
        <InputBase
          placeholder=""
          fullWidth
          classes={{
            // root: classes.inputRoot,
            input: SearchInputStyles,
          }}
          inputProps={{ 'aria-label': 'search' }}
        />
      </SearchField>
      <AccountProfileMenu />
    </TopBar>
  )
}

export default Header;
