import DelayedLoader from 'components/DelayedLoader';
import Workspace from 'components/organisms/Workspace';
import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

const NotFound = React.lazy(() => import('pages/NotFound'))
const Home = React.lazy(() => import('pages/Home'))
const Shareholders = React.lazy(() => import('pages/Shareholders'))
const Accounts = React.lazy(() => import('pages/accounts/List'))
const People = React.lazy(() => import('pages/entities/people/List'))
const Person = React.lazy(() => import('pages/entities/people/Person'))
const Account = React.lazy(() => import('pages/accounts/Detail'))

function App() {
  return (
    <Workspace>
      <React.Suspense fallback={<DelayedLoader />}>
        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/shareholders" component={Shareholders} />
          <Route
            exact
            path="/accounts/:accountId/customers/:itemId"
            render={props =>
              <Redirect to={`/accounts/${(props.match.params as any).accountId}/customers/${(props.match.params as any).itemId}/summary`} {...props} />
            }
          />
          <Route
            exact
            path="/accounts/:accountId"
            render={props =>
              <Redirect to={`/accounts/${(props.match.params as any).accountId}/business`} {...props} />
            }
          />
          <Route path="/accounts/:accountId/:tab/:itemId?/:itemTab?" component={Account} />
          <Route exact path="/entities/people/:personId" component={Person}/>
          <Route exact path="/entities/people" component={People}/>
          <Route exact path="/accounts" component={Accounts}/>
          <Route component={NotFound}/>
        </Switch>
      </React.Suspense>
    </Workspace>
  );
}

export default App;
